import React, { useState } from 'react';
import ReactQuill from 'react-quill'; // ES6
import 'react-quill/dist/quill.snow.css'; // Include the Quill CSS
import './styles.css';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons';

function RichTextEditor({ sizeValue, spaceValue, onFormSubmit, page }) {
  const [editorContent, setEditorContent] = useState('');

  const saveContent = async () => {

    try {
      const submittedAt = new Date().toLocaleString(); // Get current date and time as a string
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/create_entry/${page}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ type: "text", content: editorContent,  size: sizeValue , space: spaceValue, timestamp: submittedAt }), // This is the content of the entry
      });

      if (!response.ok) {
        throw new Error('Network response was not ok');
      }

      // Handle success
      console.log('Content saved successfully');
    } catch (error) {
      console.error('Error saving content:', error);
    }
    window.scrollTo(0, document.body.scrollHeight);
    onFormSubmit();
    setEditorContent("");
  };

  return (
    <div className="rich-text-box">
      <div className="quill">
        <ReactQuill theme="snow" value={editorContent} onChange={setEditorContent} />
      </div>
      <button className="card-buttons" onClick={saveContent}>
        <FontAwesomeIcon icon={faCheck} />
      </button>
    </div>
  );
}

export default RichTextEditor;