import React, { useState, useEffect } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTrash, faXmark, faCheck } from '@fortawesome/free-solid-svg-icons';

import Content from './Content';

import "./styles.css";

function RenamePageEntry({ page, pageName, toggleEditing, onFormSubmit }) {
  const [contentValue, setContentValue] = useState(pageName);

  const handleChange = (event) => {
    setContentValue(event.target.value);
  };

  const fetchRenamePage = async () => {
    // Submit will refresh the page
    try {
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/rename_page/${page}/${contentValue}`, {
      });
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      const newData = await response.json();
    } catch (error) {
      console.log(error.message);
    } finally {
      console.log("Page successfully renamed");
    }
  };

  return (
    <div>
      <form onSubmit={fetchRenamePage}>
        <label>
          <input className="rename-page-label" type="text" value={contentValue} onChange={handleChange} />
        </label>
        <button className="card-buttons" type="submit"><FontAwesomeIcon icon={faCheck} /></button>
        <button className="card-buttons" onClick={toggleEditing}><FontAwesomeIcon icon={faXmark} /></button>
      </form>
    </div>
  );
}

function Page({ page, pageName, data, isLoading, error, onFormSubmit }) {
  // State to manage which style is currently applied
  const [isStacked, setIsStacked] = useState(false);
  const [isEditing, setIsEditing] = useState(false);

  const fetchDeletePage = async () => {
    try {
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/delete_page/${page}`, {
      });
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      const newData = await response.json();
    } catch (error) {
      console.log(error.message);
    } finally {
      console.log("Successfully deleted page: ", page, ": ", pageName);
    }
  };

  // Function to toggle the style
  const toggleStyle = () => {
    setIsStacked(!isStacked);
  };

  const deletePage = () => {
    console.log('Deleting page');
    fetchDeletePage();
    window.location.reload();   // Refresh the page
  }

  const toggleEditing = () => {
    console.log('Renaming page');
    setIsEditing(!isEditing);
  }

  if (isLoading) return <div>Loading...</div>;
  if (error) return <div>Error: {error}</div>;

  if (!isStacked) {
    return (
      <div>
        <div className="page-title-container">
          {isEditing ? (
            <RenamePageEntry page={page} pageName={pageName} toggleEditing={toggleEditing} onFormSubmit={onFormSubmit} /> 
            ) : (
            page === 1 ? (
              <button className="button" onClick={toggleEditing}><h2 className="title">{pageName}</h2></button>
              ) : (
                <div><button className="button" onClick={toggleEditing}><h2 className="title">{pageName}</h2></button><button className="card-buttons" onClick={deletePage}><FontAwesomeIcon icon={faTrash} /></button></div>
              )
            )}
        </div>
        <ul className="card-list">
          {data.map((item, index) => (
            // Assuming each item in the list has a unique 'id' and 'name' property
            <div>
              <Content type={item.type} page={page} id={item.id} content={item.content} size={item.size} spaces={item.spaces} timestamp={item.timestamp} onFormSubmit={onFormSubmit} />
            </div>
          ))}
        </ul>
    </div>
  );
  } else {
    return (
      <div>
        <div className="page-title-container">
          {isEditing ? (
            <RenamePageEntry page={page} pageName={pageName} toggleEditing={toggleEditing} onFormSubmit={onFormSubmit} /> 
            ) : (
            page === 1 ? (
              <button className="button" onClick={toggleEditing}><h2 className="title">{pageName}</h2></button>
              ) : (
                <div><button className="button" onClick={toggleEditing}><h2 className="title">{pageName}</h2></button><button className="card-buttons" onClick={deletePage}><FontAwesomeIcon icon={faTrash} /></button></div>
              )
            )}
        </div>
        <button className="stack-button" onClick={toggleStyle}>
        <span className="svgContainer">
          Unstack
        </span>
        <span className="BG"></span>
      </button>
        <ul className="stacked-container">
          {data.slice().reverse().map((item, index) => (
            // Assuming each item in the list has a unique 'id' and 'name' property
            <div className="content">
              <Content type={item.type} id={item.id} content={item.content} size={item.size} page={page} spaces={item.spaces} timestamp={item.timestamp} onFormSubmit={onFormSubmit} />
            </div>
          ))}
        </ul>
      </div>
    );
  } 
  
}

export default Page;