import React, { useState , useEffect } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';

import "./styles.css";

function PageEntry({ onFormSubmit, toggleEditing }) {
  const [contentValue, setContentValue] = useState('');

  const handleChange = (event) => {
    setContentValue(event.target.value);
  };

  const createPage = async (event) => {
    event.preventDefault(); // Prevent the form from refreshing the page
    try {
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/create_page/${contentValue}`, {
      });
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
    } catch (error) {
      console.error('Error:', error);
    }
    toggleEditing();
    onFormSubmit();
  };

  return (
    <div>
      <form onSubmit={createPage}>
        <label>
          Enter new page name
          <input type="text" value={contentValue} onChange={handleChange} />
        </label>
        <button className="rectangular-button" type="submit">Create</button>
        <button className="rectangular-button" onClick={toggleEditing}>Cancel</button>
      </form>
    </div>
  );
}

function Sidebar({ setParentPageValue }) {
  const [data, setData] = useState([]);
  const [isEditing, setIsEditing] = useState(false);
  const [isLoading, setIsLoading] = useState(false);  // Currently unused
  const [error, setError] = useState(null);   // Currently unused

  const [currPage, setCurrPage] = useState(1);

  const fetchData = async () => {
    setIsLoading(true);
    setError(null); // Reset error state on new fetch attempt
    try {
      const response = await fetch('http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/get_pages', {
      });
      if (!response.ok) {
        throw new Error('Network response was not ok');
      }
      const newData = await response.json();
      setData(newData);
      setParentPageValue(newData[0].id, newData[0].name);
    } catch (error) {
      setError(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSelectPage = (page_id, page_name) => {
    console.log('Page selected: ', page_id);
    setCurrPage(page_id)
    setParentPageValue(page_id, page_name);
  };

  const handleCreatePage = () => {
    console.log('Creating page');
    setIsEditing(true);
  }

  const toggleEditing = () => {
    setIsEditing(!isEditing);
  }

  useEffect(() => {
    fetchData();
  }, []);

  if (isEditing) {
    return (
      <div className="sidebar">
        {data.map((item, index) => (
          <div>
            <button className={(item.id === currPage) ? 'sidebar-button active' : 'sidebar-button'} onClick={() => handleSelectPage(item.id, item.name)}>{item.name}</button>
          </div>
        ))}
        <div className="sidebar-create-entry">
        <PageEntry onFormSubmit={fetchData} toggleEditing={toggleEditing}/>
        </div>
      </div>
    );
  } else {
    return (
      <div className="sidebar">
        {data.map((item, index) => (
          <div>
            <button className={(item.id === currPage) ? 'sidebar-button active' : 'sidebar-button'} onClick={() => handleSelectPage(item.id, item.name)}>{item.name}</button>
          </div>
        ))}
        <button className="sidebar-create-button" onClick={handleCreatePage}>
          <FontAwesomeIcon icon={faPlus} />
        </button>
      </div>
    );
  }
}

export default Sidebar;