import React, { useState } from 'react';
import './styles.css';

import Edit from './Edit';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPenToSquare, faCheck, faTrash } from '@fortawesome/free-solid-svg-icons';

function Image({ id, link, spaces, size, timestamp, onFormSubmit }) {
    const [isEditing, setIsEditing] = useState(false);
    const [editorContent, setEditorContent] = useState('');

    // Click handler for the Delete button
    const handleDeleteClick = async (event) => {
        event.preventDefault(); // Prevent the form from refreshing the page
        try {
        const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/delete_entry/1/${id}`);  // Use backticks instead of single quotes to process id variable
        const data = await response.json();
        } catch (error) {
        console.error('Error:', error);
        }
        onFormSubmit();     // Reload the list of entries
    };

    // Click handler for the Edit button
    const handleEditClick = () => {
        console.log('Edit button clicked');
        setIsEditing(true);
    };

    let alt_text = "An image";

    if (isEditing) {
        return (
            <div className="card">
                <Edit onFormSubmit={onFormSubmit} type="image" id={id} current_content={link} spaces={spaces} size={size}/>
            </div>
        );
      } else {
        return (
            <div className="card">
                <div className="image-container">
                    <img src={link} alt={alt_text}></img>
                </div>
                <div className="card-button-container">
                <button className="card-buttons" onClick={handleEditClick}>
                    <FontAwesomeIcon icon={faPenToSquare} />
                </button>        
                <button className="card-buttons" onClick={handleDeleteClick}>
                    <FontAwesomeIcon icon={faTrash} />
                </button>
                </div>
            </div>
        );
      }

}

export default Image;