import React from 'react';

import TextBox from './TextBox';
import Image from './Image';
import Spaces from './Spaces';

import "./styles.css";
import RichText from './RichText';

function Content({ type, id, content, size, spaces, page, timestamp, onFormSubmit }) {

  if (type === "header") {
    return <div><Spaces num_spaces={spaces}/><TextBox text={content} id={id} spaces={spaces} timestamp={timestamp} onFormSubmit={onFormSubmit}/></div>
  } else if (type === "image") {
    return <div><Spaces num_spaces={spaces}/><Image link={content} id={id} timestamp={timestamp} onFormSubmit={onFormSubmit}/></div>
  } else if (type === "text") {
    return <div><Spaces num_spaces={spaces}/><RichText content={content} id={id} spaceValue={spaces} sizeValue={size} page={page} timestamp={timestamp} onFormSubmit={onFormSubmit}/></div>
  } else {
    <div>Error: no content able to be loaded</div>
  }
}

export default Content;