import React, { useState } from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck } from '@fortawesome/free-solid-svg-icons';

import "./styles.css";

function Edit({ onFormSubmit, id, type, current_content, spaces }) {
  const [inputValue, setInputValue] = useState('');
  const [responseMessage, setResponseMessage] = useState('');

  const handleInputChange = (event) => {
    setInputValue(event.target.value);
  };

  const handleSubmit = async (event) => {
    event.preventDefault(); // Prevent the form from refreshing the page
    try {
      const submittedAt = new Date().toLocaleString(); // Get current date and time as a string
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/edit_entry/1/${id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ content: inputValue, type: type, size: "1", timestamp: submittedAt, space: spaces }), // This is the content of the entry
      });
      const data = await response.json();
      setResponseMessage(data.message);
    } catch (error) {
      console.error('Error:', error);
      setResponseMessage('Error sending data');
    }
    onFormSubmit();
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <label>
          Enter data:
          <input type="text" value={inputValue} onChange={handleInputChange} placeholder={current_content}/>
        </label>
        <button className="card-buttons" type="submit">
          <FontAwesomeIcon icon={faCheck} />
        </button>
      </form>
      {responseMessage && <p>Response: {responseMessage}</p>}
    </div>
  );
}

export default Edit;