import React, { useState } from 'react';

import Edit from './Edit';

import "./styles.css";

function TextBox({ text, id, spaces, timestamp, onFormSubmit }) {
    const [responseMessage, setResponseMessage] = useState('');
    const [isEditing, setIsEditing] = useState(false);

    // Click handler for the Edit button
    const handleEditClick = () => {
        console.log('Edit button clicked');
        setIsEditing(true);
    };
        
    // Click handler for the Delete button
    const handleDeleteClick = async (event) => {
        event.preventDefault(); // Prevent the form from refreshing the page
        try {
          const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/delete_entry/1/${id}`);  // Use backticks instead of single quotes to process id variable
          const data = await response.json();
          setResponseMessage(data.message);
        } catch (error) {
          console.error('Error:', error);
          setResponseMessage('Error sending data');
        }
        onFormSubmit();     // Reload the list of entries
    };

    if (isEditing) {
        return (
            <div>
                <div>
                    <Edit onFormSubmit={onFormSubmit} type="header" id={id} current_content={text} spaces={spaces}/>
                </div>
            </div>
        );        
    } else {
        return (
            <div className="card">
                <div>{text}</div>
                <div>
                    <button onClick={handleEditClick}>Edit</button>
                    <button onClick={handleDeleteClick}>Delete</button>
                </div>
            </div>
        );
    }
}

export default TextBox;