import React, { useState } from 'react';
import RichTextEditor from './RichTextEditor';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faImage, faKeyboard } from '@fortawesome/free-regular-svg-icons';
import { faHeading, faCheck } from '@fortawesome/free-solid-svg-icons';

import "./styles.css";

function IconRadioButtons({ selectedValue, onChange }) {
  const [currSelectedValue, setCurrSelectedValue] = useState('');
  // Mapping of string identifiers to FontAwesomeIcon components
  const iconOptions = {
    // header: faHeading,
    text: faKeyboard,
    // image: faImage,
  };

  // Handling selection change
  const handleChange = (event) => {
    onChange(event.target.value);
    setCurrSelectedValue(event.target.value);
  };

  return (
    <div className="icon-radio-buttons">
      {Object.entries(iconOptions).map(([value, icon]) => (
        <label key={value}>
          <input
            type="radio"
            name="icon-radio"
            value={value}
            checked={currSelectedValue === value}
            onChange={handleChange}
          />
          <FontAwesomeIcon icon={icon} size="2x" className={currSelectedValue === value ? 'icon-selected' : ''} />
        </label>
      ))}
    </div>
  );
}

function SimpleTextForm({ typeValue, sizeValue, spaceValue, page, onFormSubmit }) {
  const [contentValue, setContentValue] = useState('');
  const [responseMessage, setResponseMessage] = useState('');

  const handleChange = (event) => {
    setContentValue(event.target.value);
  };

  const handleSubmit = async (event) => {
    event.preventDefault(); // Prevent the form from refreshing the page
    try {
      const submittedAt = new Date().toLocaleString(); // Get current date and time as a string
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/create_entry/${page}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ type: typeValue, content: contentValue,  size: sizeValue , timestamp: submittedAt, space: spaceValue }), // This is the content of the entry
      });
      const data = await response.json();
      setResponseMessage(data.message);
    } catch (error) {
      console.error('Error:', error);
      setResponseMessage('Error sending data');
    }
    setContentValue('')   // Clear entry box
    onFormSubmit();
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <label>
          Enter data:
          <input type="text" value={contentValue} onChange={handleChange} />
        </label>
        <button className="card-buttons" type="submit">
          <FontAwesomeIcon icon={faCheck} />
        </button>
      </form>
    </div>
  );
}

function EntryForm({ type, size, space, onSubmit, page, onFormSubmit }) {

  if (type === "header") {
    return <div><SimpleTextForm typeValue={type} sizeValue={size} spaceValue={space} onFormSubmit={onFormSubmit} page={page} /></div>
  } else if (type === "text") {
    return <div><RichTextEditor sizeValue={size} spaceValue={space} onFormSubmit={onFormSubmit} currentContent={""} page={page} /></div>
  } else if (type === "image") {
    return <div><SimpleTextForm typeValue={type} sizeValue={size} spaceValue={space}  onFormSubmit={onFormSubmit} page={page} /></div>
  } else {
    return <div></div>
  }
}

function TypeSelector({ onSelectionChange }) {
  // State to hold the selected value
  const [selectedValue, setSelectedValue] = useState('');

  // Handler to update the state with the new selected value
  const handleChange = (event) => {
    const newValue = event.target.value;
    setSelectedValue(newValue);
    onSelectionChange(newValue); // Notify the parent component
  };

  // Render the radio buttons and display the selected value
  return (
    <div className="entry-option">
      <IconRadioButtons
      selectedValue={selectedValue}
      onChange={onSelectionChange}
      />
    </div>
  );
}

