function Entry({ onFormSubmit, page }) {
  const [typeValue, setTypeValue] = useState('');
  const [responseMessage, setResponseMessage] = useState('');

  const [sizeValue, setSizeValue] = useState('');
  const [spaceValue, setSpaceValue] = useState('');

  const handleSizeChange = (newValue) => {
    setSizeValue(newValue); // Update the state with the selected value from the child
  };

  const handleSpaceChange = (newValue) => {
    setSpaceValue(newValue); // Update the state with the selected value from the child
  };

  const handleTypeInputChange = (newValue) => {
    setTypeValue(newValue);
  };

  return (
    <div className="entry-container">
        <div>
          <TypeSelector onSelectionChange={handleTypeInputChange} />

          <EntryForm type={typeValue} size={sizeValue} space={spaceValue}page={page} onFormSubmit={onFormSubmit} />
        </div>
    </div>
  );
}

export default Entry;