import React, { useState , useEffect } from 'react';

import Entry from './Entry';
import Page from './Page';
import Sidebar from './Sidebar';

import "./styles.css";

function Journal() {
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState(null);
    const [page, setPage] = useState('1');    // On first render, defaults to page id 1 and creates that starting page
    const [pageName, setPageName] = useState('');

    // Function to fetch data (initial and updates)
    const fetchData = async () => {
      console.log("Requesting get entries for page: ", page)
      setIsLoading(true);
      setError(null); // Reset error state on new fetch attempt
      try {
        const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/get_entries/${page}`, {
        });
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const newData = await response.json();
        setData(newData);
      } catch (error) {
        setError(error.message);
      } finally {
        setIsLoading(false);
      }
    };

    // Fetch data initially and after updates
    useEffect(() => {
        fetchData();
    }, [page]);

    // Function to be called by the FormComponent after submission
    const handleFormSubmit = () => {
      console.log("Refresh entry list");
        fetchData(); // Re-fetch data after form submission
    };

    const handlePageValue = (page_id, page_name) => {
      console.log('Setting page ID in parent component: ', page_id);
      setPage(page_id);
      setPageName(page_name);
    }

    return (
        <div className="journal-container">
          <Sidebar setParentPageValue={handlePageValue} />
          <div className="main-content">
            <Page page={page} pageName={pageName} data={data} isLoading={isLoading} error={error} onFormSubmit={handleFormSubmit}/>
            <Entry onFormSubmit={handleFormSubmit} page={page} />
          </div>
          <div className="footer">
          </div>
        </div>
    );
}

export default Journal;