import React from 'react';

import './styles.css';

function Spaces( {num_spaces} ) {

    if (num_spaces === "1") {
        return <div className="margin-2" />
    } else if (num_spaces === "2") {
        return <div className="margin-3" />
    } else if (num_spaces === "3") {
        return <div className="margin-4" />
    } else {
        return <div className="margin-1" />
    }
}

export default Spaces;