import React, { useState } from 'react';
import ReactQuill from 'react-quill'; // ES6
import 'react-quill/dist/quill.snow.css'; // Include the Quill CSS
import DOMPurify from 'dompurify';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPenToSquare, faCheck, faTrash, faArrowUp, faArrowDown } from '@fortawesome/free-solid-svg-icons';

import "./styles.css";

function DisplayQuillContent({ content }) {
  // Using dangerouslySetInnerHTML to render the HTML content
  return (
    <div>
      <div dangerouslySetInnerHTML={{ __html: content }} />
    </div>
  );
}

function RichText({ id, content, page, type, spaceValue, sizeValue, timestamp, onFormSubmit }) {
  const [isEditing, setIsEditing] = useState(false);
  const sanitizedContent = DOMPurify.sanitize(content);
  const [editorContent, setEditorContent] = useState('');


  const handleMoveUpClick = async (event) => {
    event.preventDefault(); // Prevent the form from refreshing the page
    try {
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/move_entry/${page}/${id}/up`);  // Use backticks instead of single quotes to process id variable
      const data = await response.json();
    } catch (error) {
      console.error('Error:', error);
    }
    onFormSubmit();     // Reload the list of entries
  };

  const handleMoveDownClick = async (event) => {
    event.preventDefault(); // Prevent the form from refreshing the page
    try {
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/move_entry/${page}/${id}/down`);  // Use backticks instead of single quotes to process id variable
      const data = await response.json();
    } catch (error) {
      console.error('Error:', error);
    }
    onFormSubmit();     // Reload the list of entries
  };

  // Click handler for the Delete button
  const handleDeleteClick = async (event) => {
    event.preventDefault(); // Prevent the form from refreshing the page
    try {
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/delete_entry/${page}/${id}`);  // Use backticks instead of single quotes to process id variable
      const data = await response.json();
    } catch (error) {
      console.error('Error:', error);
    } finally {
      onFormSubmit();     // Reload the list of entries
    }
  };

  // Click handler for the Edit button
  const handleEditClick = () => {
    console.log('Edit button clicked');
    setEditorContent(content);
    setIsEditing(true);
  };

  const saveContent = async () => {
    try {
      const submittedAt = new Date().toLocaleString(); // Get current date and time as a string
      const response = await fetch(`http://flask-env-new.eba-xituwmre.us-east-1.elasticbeanstalk.com/api/data/edit_entry/${page}/${id}`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ type: "text", content: editorContent,  size: sizeValue , timestamp: submittedAt, space: spaceValue }), // This is the content of the entry
      });

      if (!response.ok) {
        throw new Error('Network response was not ok');
      }

      // Handle success
      console.log('Content saved successfully');
    } catch (error) {
      console.error('Error saving content:', error);
    }
    onFormSubmit();
  };

  if (isEditing) {
    return (
      <div className="card">
        <div className="rich-text-box">
          <ReactQuill theme="snow" value={editorContent} onChange={setEditorContent} />
          <button className="card-buttons" onClick={saveContent}>
            <FontAwesomeIcon icon={faCheck} />
          </button>
        </div>
      </div>
      );
  } else {
    return (
      <div className="card">
        <div className="timestamp">
          {timestamp}
        </div>
        <DisplayQuillContent content={sanitizedContent} />
        <div className="card-button-container">
          <div className="card-move-buttons">
            <button className="card-buttons" onClick={handleMoveUpClick}>
              <FontAwesomeIcon icon={faArrowUp} />
            </button>
            <button className="card-buttons" onClick={handleMoveDownClick}>
              <FontAwesomeIcon icon={faArrowDown} />
            </button>   
          </div>
          <div className="card-change-buttons">     
            <button className="card-buttons" onClick={handleEditClick}>
              <FontAwesomeIcon icon={faPenToSquare} />
            </button>        
            <button className="card-buttons" onClick={handleDeleteClick}>
              <FontAwesomeIcon icon={faTrash} />
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default RichText;